<template>
 <div class="nav">
   <van-tabbar v-model="active"  :fixed="fixed" active-color="#FF8D59"   router="true">
     <van-tabbar-item icon="wap-home"  v-if="shopShow"> 门店</van-tabbar-item>
     <van-tabbar-item icon="gold-coin-o">收益</van-tabbar-item>
     <van-tabbar-item icon="balance-list-o">订单</van-tabbar-item>
<!--     <van-tabbar-item icon="friends-o">用户</van-tabbar-item>-->
   </van-tabbar>
   <div class="Content">
     <shop v-if="active==0"></shop>
     <Earning v-if="active==1"></Earning>
     <order v-if="active==2"></order>
     <user v-if="active==3"></user>
   </div>
 </div>
</template>
<script>
  const shop = () => import("../dataStat/shop/shop.vue");
  const order = () => import("../dataStat/order/order.vue");
  const Earning = () => import("../dataStat/Earnings/Earnings.vue");
  const user = () => import("../dataStat/user/user.vue");
  export default {
    components:{
      shop,
      order,
      Earning,
      user
    },
    data() {
      return {
        active:0,
        fixed:true,
        shopShow:true,
        icon: {
          normal: '//img.yzcdn.cn/icon-normal.png',
          active: '//img.yzcdn.cn/icon-active.png'
        }
      }
    },
    created(){
      var that=this;
      let path=window.location.href.split("?")[1];
      if(path!=undefined){
        let pathArr=path.split("&");
        pathArr.forEach(function (item,index) {
          let items=item.split("=");
          if(items[0]=="accountType"){
            that.$store.state.accountType=items[1];
          }else if(items[0]=="level"){
            that.$store.state.level=items[1];
          }else {
            that.$store.state.token=items[1];
            that.$store.state.token2=items[1];
          }
        })
      }
      this.storeList();
    },
    mounted(){

    },
    methods:{
      storeList(){//门店
        this.http.get2("store/stat/accountStoreList",{},this.$store.state.token2)
          .then(res => {
            if(res.data.resultCode==1){
              if(res.data.data.length>1){
                this.$store.state.shopStore=true;
              }else {
                this.$store.state.shopStore=false;
              }
              this.storeList2();
            }
          })
      },
      storeList2(){//代理商
        this.http.get2("agent/stat/childAgentList",{},this.$store.state.token2)
          .then(res => {
            if(res.data.resultCode==1){
              if(res.data.data.length>1){
                this.$store.state.agentStore=true;
              }else {
                this.$store.state.agentStore=false;
              }
              if(this.$store.state.shopStore==false&&this.$store.state.agentStore==false){
                this.shopShow=false;
              }
            }
          })
      },
    },
    watch:{
      "active":function (data){
         console.log(this.active);
      }
    }
  }
</script>

<style scoped>
.Content{
   height: 100%;
  width: 100%;
  padding-bottom: 100px;
  box-sizing: border-box;
  overflow: auto;
}
.Content::-webkit-scrollbar {
  display: none;
}
  .nav{
    height: 100%;
  }
</style>



